<template>
	<router-link :to="href" class="yd-list-item" v-if="type == 'link'">		
        <div class="yd-list-img" v-if="img">
            <slot name="img"></slot>
        </div>
        <div class="yd-list-mes">
        	<slot name="list-title"></slot>
            <div class="yd-list-title">
                <slot name="content"></slot>
            </div>
            <slot></slot>
            <slot name="other"></slot>
        </div>
    </router-link>
    <a :href="href || 'javascript:;'" class="yd-list-item" v-else-if="type == 'a'">
        <div v-if="img">
            <slot name="img"></slot>
        </div>
        <div class="yd-list-mes">
            <div class="yd-list-title">
                <slot name="title"></slot>
            </div>
            <slot></slot>
            <slot name="other"></slot>
        </div>
    </a>
    <div class="yd-list-item" v-else>
        <div class="yd-list-img" v-if="img">
            <slot name="img"></slot>
        </div>
        <div class="yd-list-mes">
            <div class="yd-list-title">
                <slot name="title"></slot>
            </div>
            <slot></slot>
            <slot name="other"></slot>
        </div>
    </div>
	
	
  
</template>

<script type="text/ecmascript-6">
 export default {
        name: 'rx-list-item',
        props: {
            type: {
                type: String,
                validator (value) {
                    return ['link', 'a', 'div'].indexOf(value) > -1;
                },
                default: 'a'
            },
            href: [String, Object],
            img:String
        }
 }

</script>

<style scoped>

</style>



