<template>
	<div class="custom" :class="{'customTab':tab}" ref="customBox">
		<ul class="nav nav-tab" v-model="index" v-show="hideBar" v-if="tab">
	  		<li
  				@touchstart = "NavItem( itemIndex - 1 )"
  				:class="{'active': index == (itemIndex - 1)}"
  				:style="{ width : customW / itemLen + 'px'  }"
  				v-for=" (item,itemIndex) in mes.title "
  			>
	  			<h1>{{item}}</h1>
	  		</li>
	  		<li :style="{transform:'translate3d'+'('+(leftDis/itemLen)+'px, 0px ,0px)',transition:transition,width : customW / itemLen + 'px' }"></li>
		</ul>
	  	<dl class="swiper" v-model="index" ref="swiper">
	  		<dd
  				:style="{transform:'translate3d'+'('+( -leftDis + (( index - 1 ) * customW) )+'px, 0px ,0px)',transition:transition}"
		  		@touchstart = "touchSwiper"
		  		@touchmove = "moveSwiper(( index - 1 ),$event)"
		  		@touchend = "upSwiper"
		  		v-for=" (item,index) in mes.page "
  			>
	  			<div v-if="showIndex > index-1">
	  				<slot :name = "item"></slot>
	  			</div>
	  		</dd>
	  	</dl>
	  	<ul class="nav" v-model="index" v-show="hideBar" v-if="!tab">
	  		<li
  				@touchstart = "NavItem(indexItem-1)"
  				:class="{'active': index == (indexItem - 1)}"
  				:style="{ width : customW / itemLen + 'px'  }"
  				v-for=" (item,indexItem) in mes.title"
  			>
  				<i class="bottom-icon" :class=" 'yd-icon-'+listData.icon[indexItem]"></i>
	  			<i 
	  				v-for=" (item1,index1) in 2 "
	  				:class=" doubleIcon ? 'yd-icon-'+listData.icon[indexItem]+'1' : 'yd-icon-'+listData.icon[indexItem] "
  					class="bottom-icon"
  					v-show="showIcon == indexItem-1 || index == indexItem-1"
  					:style="{ 'opacity': index == indexItem-1 ? op : 1-op ,'transition':transition }"
  				>
	  			</i>
	  			<h1>
	  				<span>{{item}}</span>
	  				<span
	  					v-show="showIcon == indexItem-1 || index == indexItem-1"
	  					:style="{ 'opacity': index == indexItem-1 ? op : 1-op ,'transition':transition }"
  					>{{item}}</span>
	  			</h1>
	  		</li>
  		</ul>
	  	
    </div>
</template>

<script type="text/ecmascript-6">
export default {
	name:'Move',
	data(){
		return{
			index:0,
			lock:true,
			showIndex:1,
			itemLen:4,
			leftDis:0,
			transition:'.4s',
			customW:0,
			touchX:0,
			touchY:0,
			touchT:null,
			moveX:0,
			op:1,//透明度
			showIcon:-1,
			mes:{}
		}
	},
	props:{
		tab:Boolean,
		listData:Object,
		doubleIcon:{
			type:Boolean,
			default:false
		}
	},
	computed:{
		hideBar:function(){
			return this.$store.state.pageModule.keyboard;
		}
	},
	watch:{
		
	},
	methods:{
		touchSwiper(ev){
			this.touchX = ev.changedTouches[0].clientX;
			this.touchY = ev.changedTouches[0].clientY;
			this.transition = 'none';
			this.touchT = new Date();
		},
		moveSwiper(index,ev){
			if(!this.lock){
				this.leftDis = this.customW * this.index;
				return;
			}else{
				this.moveX = ev.changedTouches[0].clientX;
				var diff = this.moveX - this.touchX;
				if(Math.abs(diff) < Math.abs(ev.changedTouches[0].clientY - this.touchY) ){ 
					return this.lock = false;
				}else if(diff>0){
					if(index == 0){
						return;
					}else{
						this.showIcon = --index; 
						this.op = 1 - Math.abs(diff) / this.customW;
					}
				}else if( diff<0){
					if(index == this.itemLen-1){
						return;
					}else{
						if( index+1 === this.showIndex ){
							this.showIndex+=1;
						}
						this.showIcon = ++index; 
						this.op = 1 - Math.abs(diff) / this.customW;
					}
				}
				this.leftDis = this.customW * this.index - (diff);
				this.$emit("move-x",'stop',this.lock);
			}
		},
		upSwiper(ev){
			this.transition='.4s';
			if(this.moveX == 0){
				return
			}else{
				var upT = new Date();
				var diff = this.moveX - this.touchX;
				var speed = diff / (upT - this.touchT);
				if(Math.abs(speed)>0.6 || diff > (this.customW/2)){
					if(speed < 0 && this.index != this.itemLen - 1){
						this.index++;
					}else if(speed > 0  && this.index != 0){
						this.index--;
					}
				}else{
					if(diff > (this.customW/2) && this.index != 0){
						this.index--;
					}else if(diff < (-this.customW/2) && this.index != this.itemLen - 1){
						this.index++;
					}
				}
				this.leftDis = this.customW * this.index ;
				this.op = 1;
				this.moveX = 0;
				this.showIcon = -1;
				this.lock = true;
				this.$emit("move-x",'go');
			};

		},
		NavItem(index){
			if(this.showIndex<=index){
				this.showIndex = index+1;
			}
			this.index = index;
			this.leftDis = this.customW * this.index ;
			this.showItemO = 1;
		}
		
	},  
	mounted:function(){
		this.$nextTick(()=>{
		    this.customW = this.$refs.customBox.getBoundingClientRect().width;
		})
	},
	created(){
		let mes = {
				len : {},
				page : {},
				title :{}
			},
			arr = Object.keys(this.listData.title); 
		this.itemLen = arr.length;
		
		for(let i = 1; i <= this.itemLen; i++){
			mes.len[i] = 'tab' + i;
			mes.page[i] = 'page' + i;
			mes.title[i] = this.listData.title[i];
		}
		this.mes = mes;
	}
}
</script>
<style scoped>
	.custom{
		overflow:hidden;
		height: 100%;
		width: 100%;
	}
	.customTab{
		height: calc(100% - 1rem);
	}
	
	.swiper{
		height:calc(100% - 1rem);
		position: relative;
		transition: .4s ;
		flex-grow: 1;
		display: flex;
		flex-direction: column;
	}
	
	.swiper>dd{
		position: absolute;
		top: 0;
		left: 0;
		overflow-x: hidden;
		overflow-y:auto ;
		flex-grow: 1;
		height: 100%;
		width: 100%;
		
	}
	
	.swiper>dd>div{
		width: 100%;
		height: 100%;
	}
	
	.nav{
		height: 1rem;
		padding: .1rem 0;
		width: 100%;
		border-top: 1px solid #f5f5f5;
		background: #fff;
		position: relative;
	}
	
	.nav::after{
		content: '';
		display: block;
		clear: both;
	}
	
	.nav-tab{
		height: 1rem;
		padding: 0;
		border-bottom: 1px solid #f5f5f5;
	}
	
	.nav-tab>li{
		padding: .3rem 0;
	}
	
	.nav>li{
		float: left;
		height: 100%;
		position: relative;
		text-align: center;
		color: #666;
	}
	
	.nav-tab>li:last-child{
		width: 25%;
		height: 2px;
		display: block;
		position: absolute;
		bottom: 0;
		left: 0;
		background: #3399ff;
		padding: 0;
	}
	
	.nav-tab>li h1{
		text-align: center;
		font-size: .28rem;
		color: #666;
	}
	
	.nav-tab .active h1{
		color: #3399ff;
	}
	
	.nav>li>i,
	.nav>li>span{
		float: left;
		width: 100%;
		text-align: center;
		font-size: .2rem;
		color: #666;
	}
	
	.nav>li>i:nth-of-type(2),
	.nav>li>i:nth-of-type(3){
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
	}
	
	.nav>.active span{
		color: #3399ff;
	}
	
	.nav>.active>i:nth-of-type(1){
		color: #666;
	}
	
	.nav>li>i:nth-of-type(1){
		display: block !important;
		opacity: 1 !important;
	}
	
	.nav>li>i:nth-of-type(2){
		opacity: 1;
		color: #3399ff
	}
	
	.nav>li>i:nth-of-type(3){
		color: #3399ff;
	}
	
	.nav>li>h1{
		float: left;
		width: 100%;
		position: relative;
	}
	
	.nav>li>h1>span{
		float: left;
		width: 100%;
		text-align: center;
		font-size: .22rem;
	}
	
	.nav>li>h1>span:nth-of-type(1){
		color: #666 !important;
	}
	
	.nav>li>h1>span:nth-of-type(2){
		position: absolute;
		top: 0;
		left: 0;
		color: #3399ff;
	}
	

</style>



