<template>
	<div>
		<div v-if="messageList.length==0" class="nodata">
			<yd-icon slot="icon" name="nodata"></yd-icon>
			<span class="noDataTips">无记录</span>
		</div>
		<yd-list theme="4" >
			<div>			
				<rx-list-item 
					v-for="message in messageList" 
					:key="message.pkId" 
					href="javascript:;" 
					type="a" 
					@click.native="showDialog(message,'allList')"
				>
					<div class="message-img">
						<span>{{getFirstLetter(message.infInnerMsg.sender)}}</span>
						<span class="condition bg-red" v-show="message.isRead != 'yes'" v-if="hideRed"></span>
					</div>
		        	<div class="item-content">
		        		
			        	<div slot="title" class="item-title">
			          		<span>{{message.fullname}}</span>
			          		<span>{{message.createTime}}</span>
			        	</div>
					    <yd-list-other slot="other">
					        <div>
					        	<span class="list-price">{{message.infInnerMsg.content}}</span>
					        </div>
					    </yd-list-other>
		        	</div>
				</rx-list-item>				
			</div>
		</yd-list>
	</div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue';
import { loadRefreshList } from '@/assets/app.js';
import listItem from '@/components/common/ListItem';
import router from '@/router/index';
Vue.component(listItem.name, listItem);
export default {
	data() {
		return {
			messageList: [],
			messageObject:{					
				infInnerMsg:{
					sender:"",
					content:""
				}
			}
		}
	},
	components: {
		
	},
	props:{
		dataType:String,
		hideRed:Boolean
	},
	methods: {
		sendMessage() {
			router.push({name:'sendMsg'});
		},
		getAllMsgList(){
			var url = _baseUrl + "/mobile/oa/get"+[this.dataType]+".do";
			this.$ajax({
				url: url,
				method: 'POST'
			}).then(response => {
				this.messageList = response.data.data.reverse();
			}).catch(error => {
				console.log(error);
			});
		},
		showDialog(message,listType){
			this.messageObject = message;
			var url = _baseUrl + "/mobile/oa/updateStatus.do";
			this.$ajax({
				url: url,
				method: 'POST',
				params:{
					pkId:this.messageObject.msgId
				}
			}).then(response => {
				if(listType=='allList'){
					this.getAllMsgList();
					this.$store.dispatch('getCountNum',this);//更新vuex数据
				}
			}).catch(error => {
				console.log(error);
			});
			this.$emit("show-msg");
			this.$store.commit('messagePopup', {
				"message":message,
				"listType":this.dataType
			});
		},
		getFirstLetter(word){
			return word.substr(0,1).toUpperCase();
		}
	},
	created(){
		this.getAllMsgList();
	}
}
</script>

<style scoped>
.yd-list-other > div>span:nth-of-type(1){
	width: 100%;
	font-size: .24rem;
}
</style>



