<template>
	<div class="customHeader">
		<popup-list v-if="showDialogWin" @hide-msg="hideMsg"></popup-list>
		<yd-navbar slot="navbar" title="消息">
			<a @click="goToIndex" slot="left">
				<yd-navbar-back-icon></yd-navbar-back-icon>
			</a>
			<span slot="right" @click="sendMessage" color="#666">新建</span>
		</yd-navbar>
		<section>
			<move :listData="iconList" :doubleIcon="true">
				<div slot="page1" class="pageBox">
					<message-list @show-msg="showPopup" dataType="AllMsgList"></message-list>
				</div>
				<div slot="page2" class="pageBox">
					<message-list @show-msg="showPopup" dataType="SendedMsgList" :hide-red="false"></message-list>
				</div>
			</move>
		</section>
	</div>
</template>

<script type="text/ecmascript-6">
	import Vue from 'vue';
	import Popuplist from '@/components/oa/Popup';
	import router from '@/router/index';
	import Move from '@/components/Move';
	import MessageList from '@/components/oa/MessageList';
	Vue.component(Popuplist.name, Popuplist);
	export default {
		data() {
			return {
				showDialogWin:false,
			    iconList:{
					icon:{
						1:"unread",
						2:"sended"
					},
					title:{
						1:"消息列表",
						2:"已发消息"
					}
				}
			}
		},
		components:{
			Move,
			MessageList
		},
		methods: {
			sendMessage() {
				router.push({name:'sendMsg'});
			},
			hideMsg(){
				this.showDialogWin = false;
			},
			getFirstLetter(word){
				return word.substr(0,1).toUpperCase();
			},
			showPopup(){
				this.showDialogWin = true;
			}
		},
		created(){
			
		}
	}
</script>

<style scoped>
.yd-list-other > div>span:nth-of-type(1){
	width: 100%;
	font-size: .24rem;
}

.customBox footer{
	display: block;
	width: 100%;
	padding: 0;
}

.customBox footer::after{
	content: '';
	display: block;
	clear: both;
}

.customBox footer a{
	float: left;
	width: 50%;
	height: 100%;
	display: block;
}

.customBox >>> footer a span{
	float: left;
	display: block;
	width: 100%;
	text-align: center;
	margin-bottom: .06rem;
}

.customBox >>> footer a span i,
.customBox >>> footer a span i::before{
	float: left;
	font-size: .55rem;
	line-height: .75rem;
	width: 100%;
}

.allmsgActive i:before{
	content: '\e694';
	color: rgb(13, 170, 246);
}

.sendedActive i:before{
	content: '\e695';
	color: rgb(13, 170, 246);
}

.allmsgActive >>> span,
.sendedActive >>> span{
	color: rgb(13, 170, 246);
}

.customBox{
	height: 100% !important;
}



</style>


