export default {
       name: 'rx-list-item',
       props: {
           type: {
               type: String,
               validator (value) {
                   return ['link', 'a', 'div'].indexOf(value) > -1;
               },
               default: 'a'
           },
           href: [String, Object],
           img:String
       }
}




