export default {
	name:'Move',
	data(){
		return{
			index:0,
			lock:true,
			showIndex:1,
			itemLen:4,
			leftDis:0,
			transition:'.4s',
			customW:0,
			touchX:0,
			touchY:0,
			touchT:null,
			moveX:0,
			op:1,//透明度
			showIcon:-1,
			mes:{}
		}
	},
	props:{
		tab:Boolean,
		listData:Object,
		doubleIcon:{
			type:Boolean,
			default:false
		}
	},
	computed:{
		hideBar:function(){
			return this.$store.state.pageModule.keyboard;
		}
	},
	watch:{
		
	},
	methods:{
		touchSwiper(ev){
			this.touchX = ev.changedTouches[0].clientX;
			this.touchY = ev.changedTouches[0].clientY;
			this.transition = 'none';
			this.touchT = new Date();
		},
		moveSwiper(index,ev){
			if(!this.lock){
				this.leftDis = this.customW * this.index;
				return;
			}else{
				this.moveX = ev.changedTouches[0].clientX;
				var diff = this.moveX - this.touchX;
				if(Math.abs(diff) < Math.abs(ev.changedTouches[0].clientY - this.touchY) ){ 
					return this.lock = false;
				}else if(diff>0){
					if(index == 0){
						return;
					}else{
						this.showIcon = --index; 
						this.op = 1 - Math.abs(diff) / this.customW;
					}
				}else if( diff<0){
					if(index == this.itemLen-1){
						return;
					}else{
						if( index+1 === this.showIndex ){
							this.showIndex+=1;
						}
						this.showIcon = ++index; 
						this.op = 1 - Math.abs(diff) / this.customW;
					}
				}
				this.leftDis = this.customW * this.index - (diff);
				this.$emit("move-x",'stop',this.lock);
			}
		},
		upSwiper(ev){
			this.transition='.4s';
			if(this.moveX == 0){
				return
			}else{
				var upT = new Date();
				var diff = this.moveX - this.touchX;
				var speed = diff / (upT - this.touchT);
				if(Math.abs(speed)>0.6 || diff > (this.customW/2)){
					if(speed < 0 && this.index != this.itemLen - 1){
						this.index++;
					}else if(speed > 0  && this.index != 0){
						this.index--;
					}
				}else{
					if(diff > (this.customW/2) && this.index != 0){
						this.index--;
					}else if(diff < (-this.customW/2) && this.index != this.itemLen - 1){
						this.index++;
					}
				}
				this.leftDis = this.customW * this.index ;
				this.op = 1;
				this.moveX = 0;
				this.showIcon = -1;
				this.lock = true;
				this.$emit("move-x",'go');
			};

		},
		NavItem(index){
			if(this.showIndex<=index){
				this.showIndex = index+1;
			}
			this.index = index;
			this.leftDis = this.customW * this.index ;
			this.showItemO = 1;
		}
		
	},  
	mounted:function(){
		this.$nextTick(()=>{
		    this.customW = this.$refs.customBox.getBoundingClientRect().width;
		})
	},
	created(){
		let mes = {
				len : {},
				page : {},
				title :{}
			},
			arr = Object.keys(this.listData.title); 
		this.itemLen = arr.length;
		
		for(let i = 1; i <= this.itemLen; i++){
			mes.len[i] = 'tab' + i;
			mes.page[i] = 'page' + i;
			mes.title[i] = this.listData.title[i];
		}
		this.mes = mes;
	}
}



